const text = Object.assign('#000000');

export default {
  blue: Object.assign('#61DAFB', { light: '#9affff', dark: '#10a8c8' }),
  purple: Object.assign('#bbbbe0', { light: '#eeedff', dark: '#8b8bae' }),
  black: text,
  white: Object.assign('#FFFFFF'),
  text,
};



